import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import './layout.css'
import Sidebar from "react-sidebar";
import HamburgerMenu from 'react-hamburger-menu';

var mql = undefined;

class Layout extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sidebarDocked: false,
      sidebarOpen: false
    };

    this.mediaQueryChanged = this.mediaQueryChanged.bind(this);
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }

  componentWillMount() {
    if(!mql) {
      mql = window.matchMedia(`(min-width: 1000px)`);
    }
    mql.addListener(this.mediaQueryChanged);
  }
  componentWillUnmount() {
    if(!mql) {
      mql = window.matchMedia(`(min-width: 1000px)`);
    }
    mql.removeListener(this.mediaQueryChanged);
  }
  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
  }
  mediaQueryChanged() {
    this.setState({ sidebarDocked: mql.matches, sidebarOpen: false });
  }

  render() {
    const docked = this.state.sidebarDocked;
    const open = this.state.sidebarOpen;
    const onSetOpen = this.onSetSidebarOpen;
    return (
      <>
        <Helmet
          title="Ingmars birthday"
        ><html lang="en" />
        </Helmet>
        <Sidebar
          sidebar={
            <div>
              {!docked && open &&
                <div style={{float: "right"}}>
                  <HamburgerMenu
                    isOpen={open}
                    menuClicked={() => {onSetOpen(!open)}}
                    width={18}
                    height={15}
                    strokeWidth={2}
                    animationDuration={0.5}
                    borderRadius={0}
                    color='white'
                  />
                  </div>}
              {this.props.sidebar}
            </div>
          }
          styles={{
            sidebar: {
              "background-color": "#444"
            }
          }}
          open={open}
          docked={docked}
          onSetOpen={onSetOpen}
        >
          {!docked &&<HamburgerMenu
              isOpen={open}
              menuClicked={() => {onSetOpen(!open)}}
              width={18}
              height={15}
              strokeWidth={2}
              animationDuration={0.5}
              borderRadius={0}
              color='white'
            />}
          <div>
            {this.props.children}
          </div>
        </Sidebar>
      </>
    )
  }
}

Layout.propTypes = {
  open: PropTypes.bool,
  docked: PropTypes.bool,
  onSetOpen: PropTypes.func,
  sidebar: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
}

export default Layout
