const fftSize = 4096
const notes = [
  [1209, 1336, 1477, 1633],
  [697, 770, 852, 941]
]
const coeffs = [0, 1, 2, 3, 0, 4, 8, 12];
const pads = [
  '1', '2', '3', 'A',
  '4', '5', '6', 'B',
  '7', '8', '9', 'C',
  '*', '0', '#', 'D'
]
let keymaps = {};
notes[0].map((freqx, idx) => {
  notes[1].map((freqy, idy) => {
    let p = pads[idy * 4 + idx];
    keymaps[p] = [freqx, freqy]
  })
})

function average(data, len) {
  let avg = 0;
  for (let i = 0; i < len; i++) {
    avg += data[i]
  }
  return avg / len
}

function listen(callback, whenDone) {
  const context = window.__context__ || new AudioContext()
  if (window.__context__) window.__context__ = context
  const analyser = context.createAnalyser()
  const distortion = context.createWaveShaper()
  const gainNode = context.createGain()
  const biquadFilter = context.createBiquadFilter()
  analyser.fftSize = fftSize
  analyser.smoothingTimeConstant = 0
  const listener = {callback, timer: undefined};

  const keys = notes.flat().map((note) => {
    return Math.round(note / (context.sampleRate / fftSize))
  });

  function analyse() {
    let len = analyser.frequencyBinCount;
    let data = new Uint8Array(len)
    analyser.getByteFrequencyData(data)
    let tones = keys.map((pos, idx) => {
      return [data[pos], idx]
    })
    tones.sort((a,b) => {return b[0]-a[0]});
    if (tones[1][0] > 100) {
      let coeff = coeffs[tones[0][1]] + coeffs[tones[1][1]];
      listener.callback(pads[coeff])
    } else {
      listener.callback(undefined)
    }
  }
  navigator.mediaDevices.getUserMedia({
    audio: true
  }).then(
    (stream) => {
      let source = context.createMediaStreamSource(stream)
      source.connect(distortion)
      distortion.connect(biquadFilter)
      biquadFilter.connect(analyser)
      listener.timer = setInterval(analyse, 200)
      if(whenDone){whenDone(listener)}
    }, console.error)
}

function play_char(char, callback, time) {
  time = time || 0.5;
  const context = window.__context__ || new AudioContext()
  if (!window.__context__) window.__context__ = context
  const sampleRate = 8000
  const buffer = context.createBuffer(2, sampleRate, sampleRate)
  const data = buffer.getChannelData(0)
  const data1 = buffer.getChannelData(1)
  if (window.__pushed__) return
  setTimeout(function() {
    window.__pushed__ = false
    if (callback) {
      callback();
    }
  }, time * 1000)
  if (!keymaps[char]) return

  for (let i = 0; i < time * sampleRate; i++) {
    data[i] = Math.sin((2 * Math.PI) * keymaps[char][0] * (i / sampleRate))
    data[i] += Math.sin((2 * Math.PI) * keymaps[char][1] * (i / sampleRate))
    data[i] *= 0.5;
    data1[i] = data[i];
  }

  const gainNode = context.createGain()
  gainNode.connect(context.destination)

  const src = context.createBufferSource()
  src.buffer = buffer
  src.connect(gainNode)
  src.start(0)

  window.__pushed__ = true
  if (!window.__context__) window.__context__ = context
}


export function call(phone_number, callback) {
  function play(char) {
    return () => {
      return new Promise((resolve, reject) => {
        play_char(char, resolve, 0.5);
      })
    }
  }
  function connect(r, char) {
    return r.then(play(char)).then(play(' '))
  }
  const res = Promise.resolve();
  return phone_number.split("").reduce(connect, res).then(callback);
}

function arraysEqual(a, b) {
if (a === b) return true;
if (a == null || b == null) return false;
if (a.length != b.length) return false;
for (var i = 0; i < a.length; ++i) {
  if (a[i] !== b[i]) return false;
}
return true;
}
function parser(callback, len) {
  len = len || 20;
  var slices = Array(len);
  return (char) => {
    if(slices[0] == char) return
    if(slices[0]) {
      slices.unshift(char);
      slices.pop();
    } else {
      slices[0] = char
      const str = slices.slice().reverse().join("");
      callback(str)
    }
  }
}
function dispatch(list) {
  return (string) => list.forEach((e) => {e(string)})
}
function dispatch_top(list_list_callback) {
  return (string) => list_list_callback.slice(-1)[0].forEach((cb) => {cb(string)})
}
export function match(expected, callback) {
  return (string) => {
    if(string.endsWith(expected)){
      callback(string)
    }
  }
}
export function listen_string(list, len, whenDone) {
  listen(parser(list, len), whenDone)
}
