import React from 'react'
import {Route, NavLink, HashRouter} from "react-router-dom";
import Layout from './layout'
import Modal from "./modal";
import GameA from './GameA';
import GameB from './GameB';
import GameC from './GameC';
import {listen_string, call, match} from './dtmf';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {synced: false, expected_sync: undefined, after_sync: undefined, progress: 0};
    this.sync_start = this.sync_start.bind(this);
    this.sync_restart = this.sync_restart.bind(this);
    this.sync_success = this.sync_success.bind(this);
    this.sync_fail = this.sync_fail.bind(this);
    this.dispatcher = this.dispatcher.bind(this);
    this.call = this.call.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.add_progress = this.add_progress.bind(this);
  }
  do_sync() {
    return (this.state.expected_sync != undefined) && (this.state.synced != this.state.expected_sync)
  }
  dispatcher(string) {
    if(this.do_sync()) {
      match("*#", this.sync_success)(string);
    } else {
      if(this.state.after_sync) {
        this.state.after_sync(string)
      } else {
      }
    }
  }
  call() {
    const expected = this.state.expected_sync;
    if(this.do_sync()) {
      call(expected + "    ", this.call);
    }
  }
  play(string, whenDone) {
    call(string, whenDone);
  }
  sync_restart() {
    this.sync_start(this.state.expected_sync, this.state.after_sync);
  }
  sync_start(expected, after_sync) {
    const next_state = {...this.state, synced: undefined, expected_sync: expected, after_sync};
    this.setState(next_state, beforeListening);
    function whenListening(listener) {
      const next_state = {...this.state, listener};
      this.setState(next_state, this.call);
    }
    function beforeListening() {
      if(!expected) {
        return;
      }
      if(this.state.listener) {
        whenListening.bind(this)(this.state.listener);
      } else {
        listen_string(this.dispatcher, 20, whenListening.bind(this));
      }
    }
  }
  sync_success() {
    if(!this.do_sync()) {
      return;
    }
    this.setState({...this.state, synced: this.state.expected_sync});
  }
  sync_fail() {
    this.setState({...this.state, expected_sync: undefined });
  }

  handleChange(event) {
    this.setState({...this.state, manual_input: event.target.value});
  }
  handleSubmit(event) {
    this.dispatcher(this.state.manual_input);
    event.preventDefault();
  }
  add_progress(levels) {
    console.log("Adding", levels)
    this.setState({...this.state, progress: (this.state.progress | levels)});
  }

  render() {
    /*
    <ul onClick={() => {this.add_progress(1)}}> First level </ul>
    <ul onClick={() => {this.add_progress(2)}}> Second level </ul>
    <ul onClick={() => {this.add_progress(4)}}> Third level </ul>
    <ul>
        <label>
          Name:
          <input type="text" name="name" value={this.state.manual_input} onChange={this.handleChange}/>
        </label>
        <input type="submit" value="Submit" onClick={this.handleSubmit}/>
        </ul>
        */
        /*
        {((this.state.progress & 1) != 0) && <div>First level</div>}
        {((this.state.progress & 2) != 0) && <div>Second level</div>}
        {((this.state.progress & 4) != 0) && <div>Third level</div>}*/
    return (
      <HashRouter>
          <Layout
            sidebar={
                <>
                <ul><NavLink exact to="/">Home</NavLink></ul>
                <ul><NavLink to="/C">Crossword</NavLink></ul>
                <ul><NavLink to="/B">Queens</NavLink></ul>
                <ul><NavLink to="/A">Game A</NavLink></ul>
                {this.state.expected_sync && !this.do_sync() && <ul><h3 className="field" onClick={this.sync_restart}>Resync!</h3></ul>}
                </>
            }
          >
              <Modal
                 show={this.do_sync()}
                 contentLabel="Sync"
              >
               <div className="game">
                  <h1 className="field" style={{color: 0xfff}}>Syncing</h1>
                  <Route render={({history}) => (
                    <button className="button" onClick={() => {this.sync_fail(); history.push("/")}}>
                      Fail
                    </button>
                  )} />
                  <button className="button" onClick={this.sync_success}>
                    Manual sync
                  </button>
                </div>
              </Modal>

                  <Modal
                     show={this.state.progress == 7}
                     contentLabel="Sync"
                  >
                     <h1 className="field" style={{color: 0xfff}}>Proficiat!</h1>
                     <h2 className="field" style={{color: 0xfff}}>Kijk in de frigo.</h2>
                  </Modal>
                <div className="content">
                <Route exact path="/" />
                <Route path="/C" render={() => <GameC sync={this} />} />
                <Route path="/B" render={() => <GameB sync={this} size={9} state={this.state.queens} />} />
                <Route path="/A" render={() => <GameA sync={this} length={4} />} />
              </div>
          </Layout>
      </HashRouter>
    );
  }
}

export default App;
