class ContentEditable extends React.Component {
    render() {
        return (<div
            onClick={this.props.onClick}
            onKeyPress={(i,j) => this.emitChange(i,j)}
            className={this.props.className}
            contentEditable={this.props.editable}
            dangerouslySetInnerHTML= {{__html: this.props.value}}></div>);
    }
    shouldComponentUpdate(nextProps) {
        return true;
    }
    emitChange(oKeyEvent) {
      oKeyEvent.preventDefault();
        if (oKeyEvent.charCode === 0 || !/[a-z]/.test(String.fromCharCode(oKeyEvent.charCode))) {
          return true;
        }
        var html = String.fromCharCode(oKeyEvent.charCode).toUpperCase();
        this.props.onChange(html);
        return true;
    }
}

function Square(props) {
  const active = (props.active ? " active" : "");
  const inactive = (props.inactive ? " inactive" : "");
  const final = (props.final ? " final" : "");
  const number = (props.number ? " number" : "");
  const classes = "square "+ active + inactive + final + number;
  return (
    <ContentEditable className={classes} onClick={props.onClick} editable={!props.inactive} onChange={props.onChange} value={props.value}>props.value</ContentEditable>
  );
}

function Field(props) {
  function filterInactive(v) {
    if(v == "/") {
      return "";
    } else {
      return v;
    }
  }
  const render = props.field.map((line,i) => {
    const render = line.map((value, j) => {
      return (<Square
        value = {filterInactive(value)}
        active = {arraysEqual(props.selected, [i,j])}
        final = {final.some((coord) => arraysEqual(coord, [i,j]))}
        inactive = {field[i][j] != " "}
        number = {field[i][j] != " " && field[i][j] != "/"}
        onClick = {() => props.onClick(i, j)}
        onChange = {(x) => props.onChange(i,j,x)}
      />)
    });
    return (
      <div className="board-row">{render}</div>);
  });
  return (<div className="field">{render}</div>);
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: [0,0],
      field: cloneDeep(field),
    };
    this.handleSelect = this.handleSelect.bind(this);
    this.onChange = this.onChange.bind(this);
    this.unlock = this.unlock.bind(this);
  }
  componentDidMount() {
    this.props.sync.sync_start();
  }
  handleSelect(i, j) {
    this.state.selected = [i, j];
    this.setState(this.state);
  }
  onChange(i,j,x) {
    this.state.field[i][j] = x;
    this.setState(this.state, this.unlock);
  }
  unlock() {
    let answer = final
      .map((s) => this.state.field[s[0]][s[1]])
      .join("");
    if(md5(answer) == "592e880590d1e7ae31530a2e191cf41d") {
      this.props.sync.add_progress(2);
    }
  }
  renderSquare(s) {
    const x = s[0];
    const y = s[1];
    const val = this.state.field[x][y];
    return (<Square value={val} />)
  }
  render() {
    function renderHint(hint) {
      return (
        <div className="board-row">
          <div className="hint number">{hint.number}.</div>
          <div className="hint">{hint.hint}</div>
        </div>
      )
    }
    const squares = final.map((s,i) => this.renderSquare(s));
    const hintsRendered = hints.map(renderHint);
    return (
      <div className="game">
        <div className="field" >
          {hintsRendered}
        </div>
          <Field
            field = {this.state.field}
            selected = {this.state.selected}
            onClick = {this.handleSelect}
            onChange = {this.onChange}
          />
          <div className="field">
            {squares}
        </div>
      </div>
    );
  }
}

export default Game;
