import React from 'react'
import Img from 'gatsby-image'
import cloneDeep from 'lodash/cloneDeep';
import Modal from "./modal";
import {dispatch, match} from './dtmf';

//1,3; 2,7; 3,2; 4,8; 5,6; 6,4; 7,1; 8,5; 9,9
const Image = () => <img width="100%" src="queen.yellow.med.png" />

function Queen(props) {
  const active = (props.active ? " active" : "");
  const error = (props.error ? " error" : "");
  const classes = "square "+ active + error;
  return (
    <button className={classes}>
      {props.queen && <Image />}
    </button>
  );
}

function findErrors(size, field) {
  return Array(size).fill(0).map((_, y) => {
    const x = field[y]
    const ret = (x == -1) || Array(size).fill(0).some((_, yy) => {
      const xx = field[yy];
      if(y == yy || xx == -1) {
        return false;
      }
      return x == xx || (x+y == xx+yy) || (x+yy == xx+y);
    });
    return ret;
  });
}

function Field(props) {
    const size = props.size;
    const field = props.field;
    const errors = findErrors(size, props.field);
    const render = Array(size).fill(0).map((_,i) => {
      const render = Array(size).fill(0).map((_, j) => {
        return (<Queen
          queen = {i === props.field[j]}
          active = {j === props.column}
          error = {errors[j] && i === props.field[j]}
          onClick = {() => props.onClick(j,i)}
        />)
      });
      return (
        <div className="board-row">{render}</div>);
    });
    return (<div className="field">{render}</div>);
}

class GameB extends React.Component {
    init(size) {
      let queens = Array(size).fill(-1);
      queens[8] = 8;
      return {
        queens,
        column: 0,
      };
    }
    constructor(props) {
      super(props);
      this.state = this.init(props.size);
      this.onClick = this.onClick.bind(this);
      this.dispatch = this.dispatch.bind(this);
      this.check_solution = this.check_solution.bind(this);
    }
    componentDidMount() {
      if(this.props.sync.state.progress & 2){
        this.props.sync.sync_start("A56*#", this.dispatch);
      } else {
        this.props.sync.sync_start();
      }
    }
    dispatch(string) {
      function calculateRow(command, prev_bitfield) {
        const ch0 = command.charCodeAt(0);
        const ch1 = command.charCodeAt(1);
        const one = '1'.charCodeAt();
        const six = '6'.charCodeAt();
        const chA =  'A'.charCodeAt();
        const zeroToSeven = (ch1 - one);
        const highNibble = prev_bitfield & (15 * 16);
        const lowNibble = prev_bitfield & 15;
        switch(command.charAt(0)){
          case '0':
            return (0 + zeroToSeven) * 16 + lowNibble;
          case '9':
            return (8 + zeroToSeven) * 16 + lowNibble;
          case '1':
          case '2':
          case '3':
          case '4':
            const char = command.charAt(1);
            if (char >= 'A' && char <= 'D') {
              const highFlip = 1 << (ch1 - chA);
              return prev_bitfield | highFlip
            } else if(char >= '6' && char <= '9'){
              const highFlip = 1 << (ch1 - six);
              return prev_bitfield & ~highFlip
            }
          default:
            return prev_bitfield;
        }
      }
      function findRowFromBitField(row) {
        let r = -1; // r will be lg(v)
        while (row != 0) // unroll for more speed...
        {
          r++;
          row = row >> 1;
        }
        return r;
      }
      let queens = this.state.queens.slice();
      if(string.endsWith("A*#")){
        this.props.sync.play("*#");
        this.setState(this.init(queens.length), this.check_solution);
        return;
      }
      const last = string.slice(-2);
      const oldrowfield = this.rowfield();
      const newrowfield = calculateRow(last, oldrowfield);
      const rowfield = newrowfield ^ oldrowfield;
      const row = findRowFromBitField(rowfield);
      const removal = rowfield & oldrowfield;
      if(removal) {
        const removalColumn = this.state.queens.findIndex((r) => r == row);
        queens[removalColumn] = -1;
      } else {
        queens[this.firstFreeColumn()] = row;
      }
      this.setState({...this.state, column: this.firstFreeColumn(queens), queens}, this.check_solution);
    }
    rowfield() {
      const queens = this.state.queens;
      let out = 0;
      for (let index = 0; index < queens.length; ++index) {
        if(index >= 8 || queens[index] < 0) {
          continue;
        }
        out |= 1 << queens[index];
      }
      return out;
    }
    firstFreeColumn(queens) {
      queens = queens || this.state.queens;
      for (let index = 0; index < queens.length; ++index) {
        if(queens[index] < 0) {
          return index;
        }
      }
    }
    onClick(i,j) {
      let queens = this.state.queens.slice();
      if(queens[i] === j) {
        queens[i] = -1;
        i = i-1;
      } else {
        queens[i] = j;
      }
      this.setState({...this.state, column: i+1, queens}, this.check_solution);
    }
    check_solution() {
      const size = this.state.queens.length;
      if(findErrors(size, this.state.queens).some(x => x)) {
        return;
      } else {
        this.props.sync.add_progress(4);
      }
    }

    render() {
      const size = this.state.queens.length;
      return (
        <div className="game">
        {!findErrors(size, this.state.queens).some(x => x) && <h1 class="field"> Winner! </h1>}
          <Field size={size} field={this.state.queens} column={this.state.column} onClick={this.onClick}/>
        </div>)
    }
}
export default GameB;
